@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/***Global defaults***/

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    transition: 0.3s ease-in-out;
}

/***Text elements styling***/
* {
    font-family: "Quicksand", sans-serif;
    font-size: 16px;
    line-height: 1.8;
    color: #080808;
}

h1, h2, h3 {
    color: #656665;
    font-weight: normal;
    line-height: 1.6;
}

h1, h1 > * {
    font-size: 28px;
}

h2, h2 > * {
    font-size: 24px;
}

h3, h3 > * {
    font-size: 20px;
}

h1 > a {
    font-weight: normal;
    color: #3a3a3a;
}

p {
    font-weight: normal;
}

h1 a,
h2 a,
h3 a,
p a,
span a {
    display: inline;
}

a {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: darkslateblue;
    font-weight: 600;
    text-decoration: none;
}

a svg {
    fill: darkslateblue;
}

a:hover {
    -webkit-filter: brightness(1.6);
            filter: brightness(1.6);
}

ul {
    list-style: none;
}

/***Images and icons***/
picture {
    display: flex;
    overflow: hidden;
}

picture source {
    display: none;
}

picture img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

svg {
    width: 22px;
}

/***Grid layout***/
/*Outermost container*/
.page-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #fefefe;
    padding: 0 0 60px 0;
}

/*Grid layout*/
.banner-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 10;
    width: 100%;
    margin: 0 0 10px 0;
    background: white;
    box-shadow: 0 2px 4px #d0d0d0;
    overflow: hidden;
}

.banner {
    width: 1280px;
}

.banner-section {
    display: flex;
}

.grid-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    width: 1280px;
}

main {
    grid-column: span 3;
    z-index: 10;
}

aside {
    grid-column: span 1;
    z-index: 10;
    padding: 0 10px;
}

main > section {
    margin: 0 0 10px 0;
    padding: 30px 0;
    /*background: white;*/
    /*border-radius: 10px;*/
    /*box-shadow: 0 1px 1px #d0d0d0;*/
}

main > nav {
    background: white;
}

aside > section {
    margin: 20px 0 20px 0;
    padding: 10px 0 10px 0;
}

.section-header {
    margin: 0 60px;
    padding: 10px 0;
}

.section-header h1 {
    font-size: 20px;
    font-weight: bold;
}

.section-header p {
    font-style: italic;
}

/*Section headers with links*/
.linked-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: thin solid lightgray;
}

.section-content {
    margin: 0 60px;
}

.scroll-container {
    /*height: 400px;*/
    /*overflow-y: scroll;*/
}

.page-navbar, .page-toolbar {
    padding: 0;
}

.page-navbar .navbar,
.page-toolbar .navbar {
    height: 60px;
}

.page-navbar .navbar a,
.page-toolbar .navbar div {
    text-transform: uppercase;
}

article {
    width: 848px;
}

.sticky-bottom {
    position: sticky;
    bottom: 10px;
    z-index: 20;
    padding: 10px 20px;
    background: rgba(255, 255, 255, 0.8);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    box-shadow: 0 0 4px #d0d0d0;
}

/***Hover tooltips***/
.tooltip {
    position: relative;
    display: inline-block;
    margin: 0;
    border-bottom: thin dotted rgba(71, 76, 89, 1);
}

.tooltip .tooltip-text {
    position: absolute;
    visibility: hidden;
    padding: 10px 20px;
    color: #dddddd;
    font-size: 14px;
    font-weight: normal;
    text-align: center;
    white-space: nowrap;
    background-color: rgba(71, 76, 89, 0.9);
    border-radius: 6px;
}

.tooltip:hover .tooltip-text {
    visibility: visible;
}

.text-positive {
    color: #2a6d3c;
}

.text-neutral {
    color: #a4650a;
}

.text-negative {
    color: #930a0a;
}
