/*Recipe showcase panel*/
.banner-panel {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 50%);
    grid-auto-rows: auto;
    grid-column-gap: 1px;
    grid-row-gap: 1px;
    width: 100%;
    min-width: 1280px;
    height: 100%;
}

.banner-panel .tile {
    border-radius: 0;
}

.tile-half {
    grid-area: 1 / 1 / 3 / 3;
}

.tile-eighth-1 {
    grid-area: 1 / 3 / 2 / 4;
}

.tile-eighth-2 {
    grid-area: 1 / 4 / 2 / 5;
}

.tile-eighth-3 {
    grid-area: 2 / 3 / 3 / 4;
}

.tile-eighth-4 {
    grid-area: 2 / 4 / 3 / 5;
}

.tile-half .tile-title {
    font-size: 30px;
}

.tile-half .tile-author {
    font-size: 18px;
}

.tile-eighth .tile-title {
    font-size: 18px;
}

.tile-eighth .tile-author {
    font-size: 16px;
}

@-webkit-keyframes sdb05 {
    0% {
        -webkit-transform: rotate(-45deg) translate(0, 0);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: rotate(-45deg) translate(-14px, 14px);
        opacity: 0;
    }
}

@keyframes sdb05 {
    0% {
        -webkit-transform: rotate(-45deg) translate(0, 0);
                transform: rotate(-45deg) translate(0, 0);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: rotate(-45deg) translate(-14px, 14px);
                transform: rotate(-45deg) translate(-14px, 14px);
        opacity: 0;
    }
}

.loader-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;

}

.panel-loader {
    min-height: 300px;
}

.section-loader, .article-loader {
    min-height: 600px;
}

.banner-container .panel-loader {
    height: 600px;
}

.loader,
.loader:before,
.loader:after {
    background: #717744;
    -webkit-animation: load1 1s infinite ease-in-out;
    animation: load1 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
}

.loader {
    color: #717744;
    text-indent: -9999em;
    margin: 88px auto;
    position: relative;
    font-size: 11px;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
}

.loader:before,
.loader:after {
    position: absolute;
    top: 0;
    content: '';
}

.loader:before {
    left: -1.5em;
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
}

.loader:after {
    left: 1.5em;
}

@-webkit-keyframes load1 {
    0%,
    80%,
    100% {
        box-shadow: 0 0;
        height: 4em;
    }
    40% {
        box-shadow: 0 -2em;
        height: 5em;
    }
}

@keyframes load1 {
    0%,
    80%,
    100% {
        box-shadow: 0 0;
        height: 4em;
    }
    40% {
        box-shadow: 0 -2em;
        height: 5em;
    }
}

